.wrapper {
  .navItem {
    .iconWrap {
      &:last-child {
        margin-bottom: 10px;
      }
      padding: 12px;
      border-radius: 4px;
      display: flex;
      align-items: center;
      transition: 0.3s ease;
      &:hover {
        background-color: rgba(22, 24, 35, 0.03);
      }
      .icon {
        font-size: 2.5rem;
      }
      .followLink,
      span {
        font-size: 1.8rem;
        display: inline-block;
        margin-left: 8px;
      }
    }
  }
  .loginWrapper {
    position: relative;
    padding: 16px 8px 0 8px;
    &::before {
      content: '';
      position: absolute;
      height: 1px;
      left: 8px;
      right: 8px;
      background: rgba(22, 24, 35, 0.12);
      transform: scaleY(0.5);
      top: 0;
    }
    .desc {
      color: #16182380;
      line-height: 1.6;
      font-size: 1.5rem;
    }
  }
}
